<template>
    <div class="content">
        <template v-if="playPl2">
            <div class="fr-layout">
                <div class="four">
                    <div class="item mui-scroll-wrapper" v-for="(item, index) in playPl2" :key="index">
                        <ul class="mui-scroll">
                            <li @click="selectTouzhu" v-for="(item1, index1) in item.sscPlayPlList" :key="index1">
                                <strong>{{item1.name}}</strong>
                                <span>{{item1.playPl}}</span>
                                <input hidden class="name" :value="item['name'] + '-' + item1.name" type="text">
                                <input hidden class="playPl" :value="item1.playPl" type="text">
                                <input hidden class="playPlId" :value="item1.playPlId" type="text">
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </template>
        <loadding v-else></loadding>
    </div>
</template>

<script>

    import { mapState } from "vuex";

    import { 
        GAME_GET_SSC_PLAY_PL2
    } from "@/tool/common/constants.js";

    import $ from "jquery";

    export default {
        name: 'Play3',
        props:['playId'],
        data(){
            return {
                left: 0,
                playPl2: false,
            }
        },
        created(){
            this.getSscPlayPl2()
        },
        computed: {
            ...mapState('Game', {
                playGroupId: state => state.playGroupId,
                number: state => state.getSscOpenTime2.number,
                chip: state => state.chip,
            })
        },
        methods: {
            // 选择投注码
            selectTouzhu(e){
                var $even = e.target.parentNode.classList
                if($even.contains('active')){
                     $even.remove('active')
                } else{
                    $even.add('active')
                }
                this.sscBetList()
            },
            // 所选注数
            sscBetList(){
                var _this = this
                var sscBetList = []
                $('.content .fr-layout ul li.active').each(function(){
                    var playPlId = $(this).find(".playPlId").val()
                    var playPl = $(this).find(".playPl").val()
                    var name = $(this).find(".name").val()
                    var zhushu = 1
                    var zhu = {
                        "number": _this.number,
                        "playGroupId": _this.playGroupId,
                        "beishu":1,
                        "zhushu":zhushu,
                        "content": name,	
                        "playId": _this.playId,
                        "playPl":playPl,
                        "playPlId":playPlId,
                        "totalMoney":_this.chip,
                        "perMoney":_this.chip * zhushu,
                    }
                    sscBetList.push(zhu)
                })
                this.$store.commit('Game/SET_SSC_BET_LIST', sscBetList)
            },
            // 当前玩法赔率
            getSscPlayPl2(){
                var data = {
                    playId: this.playId
                }
                this.$api.game[GAME_GET_SSC_PLAY_PL2](data)
                .then(res=>{
                    this.playPl2 = res.sscPlayPlGroupList
                    setTimeout(() => {
                        this.mui(".mui-scroll-wrapper").scroll({
                            scrollY: true, //是否竖向滚动
                            scrollX: false, //是否横向滚动
                            startX: 0, //初始化时滚动至x
                            startY: 0, //初始化时滚动至y
                            indicators: true, //是否显示滚动条
                            deceleration: 0.0006, //阻尼系数,系数越小滑动越灵敏
                            bounce: true //是否启用回弹
                        })
                    }, 300);
                })
            }
        },
        destroyed(){
            console.log('play3 destroyed');
        }
    }
</script>

