<template>
    <div>
        <!-- 选项卡 -->
        <div class="tabs">
            <div class="box" :style="{'transform':`translateX(-${tabs > 2 ? (tabs - 1)*2.4 : 0}rem)`}">
                <div class="line" :style="{'left': `${tabs * 2.4}rem`}"></div>
                <span @click="togglePlay(0)" :class="{'active': tabs == 0}">双面</span>
                <span @click="togglePlay(1)" :class="{'active': tabs == 1}">排名1~10</span>
                <span @click="togglePlay(2)" :class="{'active': tabs == 2}">冠亚和</span>
            </div>
        </div>
        <!-- 三种玩法 对应三个子组件 -->
        <div class="sub-play-wrapper">
            <keep-alive>
                <play1 v-if="tabs==0"
                :playId="playId[tabs]"
                >
                </play1>

                <play2 v-if="tabs==1"
                :playId="playId[tabs]"
                >
                </play2>

                <play3 v-if="tabs==2"
                :playId="playId[tabs]"
                >
                </play3>
            </keep-alive>
        </div>
    </div>
</template>

<script>

    import $ from "jquery";

    import {
        
    } from "@/tool/common/constants.js";

    // 双面
    import play1 from "./play/play1.vue";
    // 数字
    import play2 from "./play/play2.vue";
    // 龙虎
    import play3 from "./play/play3.vue";

    export default {
        data(){
            return {
                playId: [494, 496, 495],
                tabs: 0,
            }
        },
        watch: {

        },
        created(){
            
        },
        mounted(){
            
        },
        computed: {
                        
        },
        methods: {
            // 切换玩法
            togglePlay(index){
                this.tabs = index
                this.clearSelect()
            },
            // 清除选项
            clearSelect(){
                $('.fr-layout li').removeClass('active')
                this.$store.commit('Game/CLEAR_BET_LIST')
            },
        },
        components: {
            play1,
            play2,
            play3,
        }
    }
</script>

